import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import './stylesProductList.scss';

const AdminList = ({ data, title, click, setProduct }) => (
  <div className="product-list-box">
    <div className="admin-title">
      <h2>{title}</h2>
    </div>
    {data.length === 0 ? <p>Rien à afficher pour l'instant.</p> : null}
    {data.map((product) => (
      <div className="product-list-item" key={product.id}>
        <div className="item-info">
          {product.id ? `${product.id} /` : null}
          {product.name ? ` ${product.name} /` : null}
          {product.synopsys ? ` ${product.synopsys.slice(0, 25)}... /` : null}
          {product.price ? ` ${product.price}€ /` : null}
          {product.stock_quantity ? ` stock : ${product.stock_quantity}` : null}
        </div>
        <div className="item-modify">
          <FontAwesomeIcon
            icon="file-image"
            onClick={() => {
              click();
              setProduct(product);
            }}
          />
          <FontAwesomeIcon
            icon="pen"
            onClick={() => {
              click();
              setProduct(product);
            }}
          />
        </div>
      </div>
    ))}
  </div>
);

export default AdminList;
